/*-------------------------------------------------------------------------------
 1. Global
-------------------------------------------------------------------------------*/
/* 1.1 General */
@import url("../css/bootstrap.css");
@import url("../css/animsition.css");
@import url("../css/font-awesome.min.css");
@import url("../css/ionicons.min.css");
@import url("../css/linearicons.css");
@import url("../css/animate.css");
@import url("../css/magnific-popup.css");
@import url("../css/jquery.fullPage.css");
@import url("../css/jquery.pagepiling.css");
@import url("../css/owl.carousel.css");
@import url("../css/layers.css");
@import url("../css/navigation.css");
html {
  font-size: 12px;
  -webkit-font-smoothing: antialiased;
}

body {
  font-family: 'Poppins', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0.025em;
  line-height: 2.1428;
  color: #666666;
  -webkit-overflow-scrolling: touch;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-thumb {
  cursor: pointer;
  background: #00adee;
}

::selection, -webkit-::selection, ::-moz-selection {
  background-color: #00adee;
  color: #fff;
}

/* 1.2 Typography */
@font-face {
  font-family: 'Poppins';
  src: url("../fonts/poppins-regular-webfont.woff2") format("woff2"), url("../fonts/poppins-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

h1, .h1 {
  font-size: 2.5rem;
  line-height: 1.2;
  font-weight: 600;
  color: #4d4a4c;
  letter-spacing: -0.025em;
  margin: 0.5em 0;
}

h2, .h2 {
  font-weight: 600;
  font-size: 2.571rem;
  line-height: 1.166;
  color: #4d4a4c;
  letter-spacing: 0;
  margin: 2.6rem 0;
}

h3, .h3 {
  font-weight: 600;
  font-size: 2.142rem;
  line-height: 1.2;
  letter-spacing: -0.05em;
  color: #4d4a4c;
  margin: 2.6rem 0;
}

h4, .h4 {
  font-weight: 600;
  font-size: 1.714rem;
  line-height: 1.25;
  letter-spacing: 0.025em;
  color: #4d4a4c;
  margin: 2.6rem 0;
}

h5, .h5 {
  font-weight: 600;
  font-size: 1.571rem;
  line-height: 1.1;
  letter-spacing: 0;
  color: #4d4a4c;
  margin: 2.6rem 0;
}

h6, .h6 {
  font-weight: 600;
  font-size: 1.285rem;
  line-height: 1.3333;
  letter-spacing: 0;
  color: #4d4a4c;
  margin: 2.6rem 0;
}

h1 a, .h1 a, h2 a, .h2 a, h3 a, .h3 a, h4 a, .h4 a, h5 a, .h5 a, h6 a, .h6 a {
  color: inherit;
}

h1 a:hover, .h1 a:hover, h2 a:hover, .h2 a:hover, h3 a:hover, .h3 a:hover, h4 a:hover, .h4 a:hover, h5 a:hover, .h5 a:hover, h6 a:hover, .h6 a:hover {
  text-decoration: none;
}

strong {
  font-weight: 600;
}

blockquote {
  margin-bottom: 2.6rem;
  font-size: 1.714rem;
  letter-spacing: 0.025em;
  line-height: 1.5;
  padding: 0;
  width: 80%;
  padding-left: 3rem;
  color: #000;
}

cite {
  display: block;
  color: #000;
  font-weight: 600;
  letter-spacing: 0;
  font-style: normal;
}

.blockquote-cite {
  font-size: 1rem;
  color: #999999;
  letter-spacing: 0.05em;
  padding-left: 7.1rem;
  margin-bottom: 5rem;
}

.blockquote-cite cite {
  font-size: 1.14rem;
}

a {
  color: #00adee;
  transition: color .3s ease-out;
}

a:hover {
  color: #00adee;
  outline: none;
}

a:focus {
  color: #00adee;
  outline: none;
  text-decoration: none;
}

/* 1.3 Fields */
input[type='text'], input[type='address'], input[type='url'], input[type='password'], input[type='email'], input[type="search"], input[type="tel"] {
  display: inline-block;
  width: 100%;
  font-size: 1rem;
  border-radius: 0.75em;
  color: #3d3d3d;
  background-color: #fff;
  border: 0;
  border: 1px solid #d1d1d1;
  border-radius: 0;
  padding: 0.2rem 1rem 0;
  height: 3.6em;
  box-shadow: none;
  -webkit-appearance: none;
  transition: all .15s;
}

textarea {
  display: inline-block;
  width: 100%;
  font-size: 1rem;
  border-radius: 0.75em;
  color: #3d3d3d;
  background-color: #fff;
  border: 0;
  border: 1px solid #d1d1d1;
  border-radius: 0;
  padding: 0.2rem 1rem 0;
  height: 3.6em;
  box-shadow: none;
  -webkit-appearance: none;
  transition: all .15s;
  padding-top: 0.8rem;
  height: 15rem;
  resize: none;
  transition: all .3s;
}

input[type='text']:focus, input[type='address']:focus, input[type='url']:focus, input[type='password']:focus, input[type='email']:focus, input[type="search"]:focus, input[type="tel"]:focus {
  border-color: #00adee;
  outline: 0;
}

textarea:focus {
  border-color: #00adee;
  outline: 0;
}

input.input-gray, textarea.input-gray {
  background: #f6f6f6;
}

input.input-gray:focus, textarea.input-gray:focus {
  background: #fff;
}

input::-moz-placeholder {
  color: #999999;
  opacity: 1;
}

input:-ms-input-placeholder, input::-webkit-input-placeholder {
  color: #999;
}

textarea::-moz-placeholder {
  color: #999;
  opacity: 1;
}

textarea:-ms-input-placeholder, textarea::-webkit-input-placeholder {
  color: #999;
}

.form-group {
  margin-bottom: 30px;
}

input.error {
  border-color: #00adee;
}

.mc-label, label.error {
  font-size: 0.83em;
  font-weight: 400;
  margin: 0.7em 0 0 0;
  color: #747474;
}

/* 1.4 Buttons */
.btn {
  color: #fff;
  letter-spacing: 0.025em;
  font-size: 1rem;
  display: inline-block;
  border: 0;
  border-radius: 0;
  padding: 1.1em 2.28em 1em;
  background-color: #000;
  text-decoration: none;
  transition: all 0.3s ease-out !important;
}

.btn:hover, .btn:focus {
  background-color: #00adee;
  color: #fff;
  box-shadow: none;
  outline: none !important;
}

.btn-upper {
  font-weight: 600;
  text-transform: uppercase;
}

.btn-yellow {
  background: #00adee;
  color: #000;
}

.btn-yellow:hover, .btn-yellow:focus {
  background: #000;
}

/* 1.5 Icons */
.icon {
  display: inline-block;
}

.icon-medium {
  font-size: 4.2rem;
}

.link-arrow {
  display: inline-block;
  color: #000;
  line-height: 1 !important;
  font-weight: 600;
}

.link-arrow-2 {
  display: inline-block;
  font-size: 1.14rem;
  color: #999999;
  font-weight: 400;
}

.link-arrow .icon, .link-arrow-2 .icon {
  position: relative;
  margin-left: 0.7rem;
  font-size: 2.1em;
  vertical-align: middle;
  transition: transform 0.3s;
}

.link-arrow .icon {
  top: 0.1rem;
}

.link-arrow .ion-ios-arrow-right {
  font-size: 1.5em;
}

.link-arrow-2 .ion-ios-arrow-right {
  font-size: 1.5em;
}

.link-arrow:hover, .link-arrow-2:hover {
  color: #00adee;
  text-decoration: none;
}

.link-arrow:hover .icon, .link-arrow-2:hover .icon {
  transform: translateX(0.5rem);
}

/* 1.6 Preloader */
.animsition {
  position: absolute;
  z-index: 100;
  left: 0;
  width: 100%;
  top: 0;
  height: 100%;
}

.preloader {
  position: fixed;
  overflow: hidden;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  color: #4b4b4b;
  text-align: center;
}

.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  transform: translate(-50%, -50%);
}

.double-bounce1 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #00adee;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  animation: sk-bounce 2.0s infinite ease-in-out;
}

.double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #00adee;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  animation: sk-bounce 2.0s infinite ease-in-out;
  animation-delay: -1.0s;
}

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

/* 1.7 Helpers */
.text-dark {
  color: #000;
}

.text-muted {
  color: #999;
}

.text-white {
  color: #fff;
}

.text-primary {
  color: #00adee;
}

.masked {
  position: relative;
}

.masked:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.1;
}

.pt-0 {
  padding-top: 0 !important;
}

.pb-0 {
  padding-bottom: 0 !important;
}

.rel-1 {
  position: relative;
  z-index: 1;
}

.overflow-hidden {
  overflow: hidden;
}

/*-------------------------------------------------------------------------------
  2. Navbar
-------------------------------------------------------------------------------*/
.navbar {
  position: absolute;
  z-index: 10;
  left: 0;
  top: 0;
  width: 100%;
  padding-top: 2.7rem;
  height: 7rem;
  background-color: #fff;
  margin: 0;
  border-radius: 0;
  /*border-bottom: 1px solid #f0f0f0;*/
}

.navbar .brand {
  float: left;
  margin-top: -25px;
  margin-left: -1px;
  transition: none;
}

@media screen and (max-width: 768px) {
  .navbar .brand {
    margin-top: -20px;
  }
}

/* 2.1 Navbar elements */
.brand {
  line-height: 1.6;
  color: #000;
  display: inline-block;
}

a.brand:hover, a.brand:focus {
  text-decoration: none;
  color: #000;
}

.brand img {
  display: inline-block;
  vertical-align: top;
  margin-right: 0.65rem;
  height: 5rem;
}

.brand-info {
  display: inline-block;
}

.brand-name {
  font-weight: 600;
  font-size: 1.714rem;
  line-height: 1;
}

.brand-text {
  color: #999999;
  font-size: 0.857rem;
  font-weight: 300;
  margin-top: -0.3rem;
}

.navbar-toggle {
  display: block;
  margin: 0;
  padding: 0;
  border-radius: 0;
}

.navbar-toggle .icon-bar {
  background-color: #4d4a4c;
  transition: all 0.3s ease;
}

.navbar-toggle:hover .icon-bar {
  background-color: #00adee;
}

.navbar .social-list {
  float: right;
  margin-right: 10%;
}

.social-list {
  overflow: hidden;
}

.social-list a {
  float: left;
  font-size: 1.428rem;
  line-height: 1;
  color: #4d4a4c;
  margin-left: 2rem;
}

.social-list a:first-child {
  margin-left: 0;
}

.social-list a:hover {
  color: #00adee;
  text-decoration: none;
}

.navbar-spacer {
  float: right;
  margin: -1rem 4.5rem 0;
  width: 1px;
  height: 3.3rem;
  background-color: #e0e0e0;
}

.navbar-address {
  float: right;
  font-weight: 600;
  text-transform: uppercase;
}

/* 2.2 Navbar affix */
.navbar.affix {
  position: fixed;
  box-shadow: 0 0 65px rgba(0, 0, 0, 0.07);
}

/* 2.3 Sidebar menu */
.click-capture {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  z-index: 99;
}

.menu {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  z-index: 100;
  background-color: #fff;
  transform: translateX(100%);
  transition: all 0.6s ease;
}

.close-menu {
  position: absolute;
  z-index: 2;
  top: 2.3rem;
  font-size: 1.6rem;
  line-height: 1;
  color: #000;
  cursor: pointer;
  transition: all 0.3s ease;
}

.close-menu:hover {
  color: #00adee;
}

.menu .menu-lang {
  opacity: 0;
  position: absolute;
  opacity: 0;
  left: 15%;
  top: 3rem;
  text-transform: uppercase;
  transform: translateY(3rem);
  transition: transform 0.5s 0.4s ease, opacity 0.5s 0.4s ease, color 0.3s ease;
}

.menu-lang a {
  display: inline-block;
  margin-left: 1.5rem;
  color: #999999;
}

.menu-lang a:first-child {
  margin-left: 0;
}

.menu-lang .active, .menu-lang a:hover {
  color: #00adee;
  text-decoration: none;
}

.menu-list {
  opacity: 0;
  position: absolute;
  left: 0;
  top: 9rem;
  bottom: 10.5rem;
  padding-left: 15%;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
  font-weight: 600;
  letter-spacing: -0.025rem;
  list-style: none;
  margin: 0;
  transform: translateY(3rem);
  transition: transform 0.5s 0.6s ease, opacity 0.5s 0.6s ease;
}

.menu-list li a {
  color: #999999;
}

.menu-list .active > a, .menu-list li a:hover {
  text-decoration: none;
  color: #00adee;
}

.menu-list > li {
  font-size: 1.95rem;
  line-height: 1;
  margin-top: 1.8rem;
}

.menu-list > li:first-child {
  margin-top: 0;
}

.menu-list ul {
  display: none;
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu-list ul li {
  font-size: 1.142rem;
  margin-top: 1.142rem;
}

.menu-list ul li:first-child {
  margin-top: 2.4rem;
}

.menu-footer {
  position: absolute;
  left: 15%;
  bottom: 4rem;
}

.menu-footer .social-list {
  opacity: 0;
  transform: translateY(3rem);
  transition: all 0.5s 0.7s ease;
}

.menu-footer .social-list a {
  color: #cccccc;
  margin: 0;
  margin-right: 2.7rem;
}

.menu-footer .social-list a:last-child {
  margin-right: 0;
}

.menu-footer .social-list a:hover {
  color: #00adee;
}

.menu-footer .copy {
  opacity: 0;
  display: none;
  margin-top: 3.2rem;
  color: #000;
  line-height: 1.71rem;
  transform: translateY(2rem);
  transition: all 0.5s 0.9s ease;
}

.menu-is-opened .click-capture {
  visibility: visible;
}

.menu-is-opened .menu {
  transform: translateX(0);
  box-shadow: 0 0 65px rgba(0, 0, 0, 0.07);
}

.menu-is-opened .menu .menu-lang {
  transform: translate(0);
  opacity: 1;
}

.menu-is-opened .menu-list {
  transform: translate(0);
  opacity: 1;
}

.menu-is-opened .menu .menu-footer .social-list, .menu-is-opened .menu .menu-footer .copy {
  transform: translate(0);
  opacity: 1;
}

.menu-is-closed .menu .menu-lang, .menu-is-closed .menu .menu-list {
  opacity: 0;
  transition-delay: 0s;
}

.menu-is-closed .menu .menu-footer .social-list, .menu-is-closed .menu .menu-footer .copy {
  opacity: 0;
  transition-delay: 0s;
}

/*-------------------------------------------------------------------------------
  3. Homepage
-------------------------------------------------------------------------------*/
.wrapper {
  padding-top: 7rem;
  overflow-x: hidden;
}

.boxed {
  padding-left: 1.7rem;
  padding-right: 1.7rem;
}

.boxed .container, .boxed .container-fluid {
  padding: 0;
  max-width: 100%;
}

.left-boxed {
  left: 1.7rem;
}

.right-boxed {
  right: 1.7rem;
}

.section {
  padding: 5rem 0;
}

.section-title {
  margin: 0;
}

.section-title .link-arrow-2 {
  margin-top: 1.05rem;
}

.section-subtitle {
  display: inline-block;
  font-weight: 500;
  font-size: 1.142rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #00adee;
  margin-bottom: 1rem;
}

/* 3.1 Jumbotron */
.jumbotron {
  position: relative;
  overflow: hidden;
  padding: 0;
  margin: 0;
  color: #fff;
  background-color: #fff;
}

/* 3.2 Slider */
.rev_slider .link-arrow {
  color: #fff;
}

.rev_slider .link-arrow:hover {
  color: #00adee;
}

.rev_slider .link-arrow:hover .icon {
  transform: none;
}

.slider-prev, .slider-next {
  position: absolute;
  top: 50%;
  z-index: 1;
  font-size: 4rem;
  color: #fff;
  cursor: pointer;
  transition: all 0.3s;
  transform: translateY(-50%);
}

.slider-prev {
  left: 2.2rem;
}

.slider-next {
  right: 2.2rem;
}

.slider-prev:hover, .slider-next:hover {
  color: #00adee;
}

/* 3.3 Section About */
.section-about {
  padding: 8rem 0 0;
}

.section-about-title {
  margin-bottom: 5rem;
}

.dots-image {
  display: inline-block;
  position: relative;
}

.dots-image img {
  display: block;
  position: relative;
  z-index: 1;
}

.dots-image .dots {
  position: absolute;
  left: -20%;
  bottom: -15%;
  width: 101%;
  height: 57.2%;
  background: url(../images/bg/dots.png) 0 0 repeat;
}

.experience-box {
  position: relative;
  white-space: nowrap;
  margin: 6rem 0;
}

@media (min-width: 992px) {
  .experience-box {
    margin-top: 6rem;
  }
}

@media (min-width: 1200px) {
  .experience-box {
    margin-top: 12.2rem;
  }
}

.experience-border {
  display: inline-block;
  vertical-align: middle;
  width: 12.15rem;
  height: 12.15rem;
  border: 0.357rem solid #00adee;
}

.experience-content {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  left: -8rem;
  top: 0.2rem;
}

.experience-number {
  display: inline-block;
  vertical-align: middle;
  font-size: 7.142rem;
  line-height: 1;
  font-weight: 600;
  color: #000;
}

.experience-info {
  display: inline-block;
  vertical-align: middle;
  margin: -1rem 0 0 0.8rem;
  font-size: 1.57rem;
  line-height: 1.181;
  color: #000;
}

/* 3.4 Section Projects */
.section-projects {
  padding-bottom: 8rem;
}

.filter-content {
  position: relative;
  overflow: hidden;
}

.filter {
  list-style: none;
  font-size: 1.142rem;
  font-weight: 600;
  color: #000;
  padding-left: 0;
  margin: 0;
}

.filter li {
  margin: 0.6rem 2.6rem 0 0;
  display: inline-block;
}

.filter li:last-child {
  margin-right: 0;
}

.filter li a {
  color: inherit;
}

.filter li a:hover {
  color: #00adee;
  text-decoration: none;
}

.filter .active a {
  color: #00adee;
  text-decoration: none;
}

.section-projects .filter-content {
  margin: 2rem 0 0 0;
}

.section-projects .filter {
  margin: 0;
}

.section-projects .view-projects {
  margin: 0.85rem 0 0 0;
}

.view-projects {
  display: inline-block;
  font-style: italic;
  color: #999999;
}

.view-projects:hover {
  text-decoration: none;
  color: #00adee;
}

/* 3.4.1 Projects Carousel */
.project-carousel {
  margin-top: 5.92rem;
}

.project-item {
  position: relative;
  overflow: hidden;
  transition: all 0.3s;
}

.project-item img {
  width: 100%;
}

.item-shadow {
  position: relative;
}

