    width: 50%;
  }
  /* Homepage */
  .boxed {
    padding-left: 3rem;
    padding-right: 3rem;
  }
  .boxed-inner {
    padding-left: 0;
  }
  .right-boxed {
    right: 3rem;
  }
  .left-boxed {
    left: 3rem;
  }
  /* Section About */
  .section-about {
    padding-top: 12rem;
  }
  /* Section Projects */
  .section-projects .filter {
    margin-right: 14rem;
  }
