  .footer-flex .flex-item {
    flex: 0 0 33.3333%;
  }
}

/* 8.2 Min width 992px */
@media (min-width: 992px) {
  html {
    font-size: 14px;
  }
  /* Helpers */
  .pull-md-right {
    float: right;
  }
  /* Sidebar Menu */
  .menu {
    width: 35%;
  }
  /* Homepage */
  .boxed {
    padding-left: 4.2857rem;
    padding-right: 4.2857rem;
  }
  .right-boxed {
    right: 4.2857rem;
  }
  .left-boxed {
    left: 4.2857rem;
  }
  /* Homepage v2 */
  .boxed-inner .boxed {
    padding-left: 6rem;
  }
  .service-list {
    margin-bottom: 0;
  }
  .col-contact-info {
    border-top: 0;
    padding-top: 0;
  }
  .row-contact-info {
    margin-top: -5rem;
  }
  .col-contact-info {
