.slider-zoom {
  position: absolute;
  bottom: 50px;
  right: 50px;
  z-index: 999;
  width: 35px;
  height: 35px;
  color: #fff;
  cursor: pointer;
  line-height: 40px;
  font-size: 14px;
  text-align: center;
}

.slider-zoom i, .slider-zoom:before, .slider-zoom:after, .slider-zoom, .herolink {
  transition: all 200ms linear;
}

.slider-zoom:before, .slider-zoom:after {
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  z-index: -1;
}

.slider-zoom:before {
  top: 0;
  left: 0;
  border-left: 1px solid #fff;
  border-top: 1px solid #fff;
}

.slider-zoom:after {
  bottom: 0;
  right: 0;
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
}

.slider-zoom:hover:before {
  top: -5px;
  left: -5px;
}

.slider-zoom:hover:after {
  bottom: -5px;
  right: -5px;
}

.slider-zoom:hover i {
  transform: rotate(90deg);
}

