  text-align: center;
  cursor: pointer;
  color: #fff;
  font-size: 2rem;
  font-weight: 600;
  opacity: 1;
  transition: all 200ms linear;
  z-index: 20;
}

.owl-carousel.project-details .owl-next {
  right: 5rem;
  padding-left: 2px;
}

.owl-carousel.project-details .owl-prev {
  left: 5rem;
  padding-right: 2px;
}

/* 3.4.1 Projects Carousel */
.project-carousel .project-item {
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}

.project-carousel .project-item img {
  width: 100%;
  transition: all 2s ease;
  filter: contrast(105%) brightness(105%);
}

.project-carousel .project-item:hover img {
  transform: scale(1.1);
}

.project-carousel .project-item:hover .project-hover {
  background-color: #00adee;
}

.project-carousel .project-item:hover .project-hover .project-title {
  position: relative;
  color: #00adee;
