  transition: all 0.5s ease-in-out;
}

.project-carousel .project-item:hover .project-hover .project-title a:hover {
  color: #00adee;
}

.project-carousel .project-item:hover .project-hover .project-description {
  opacity: 1;
  transform: translateY(0);
  transition: all 0.4s 0.2s ease;
}

.project-carousel .project-item:hover .link-arrow {
  color: #00adee;
}

.owl-nav {
  position: absolute;
  width: 100%;
  top: calc(50% - 25px);
}

.owl-prev {
  position: absolute;
  left: 0;
  margin: 0;
  transform: translateX(-150%);
}

.owl-next {
  position: absolute;
  right: 0;
  margin: 0;
  transform: translateX(150%);
}

.owl-nav .disabled {
  color: #bcbcbc;
}

.owl-prev,
.owl-next {
  font-size: 3.5rem;
  line-height: 1;
  transition: all 0.5s;
  cursor: pointer;
}

.owl-prev:before {
  font-family: "Ionicons";
  content: "\f3d2";
}

.owl-next:before {
  font-family: "Ionicons";
  content: "\f3d3";
}

.owl-prev:hover,
.owl-next:hover {
  color: #00adee;
}

.owl-dots {
  text-align: center;
  margin-top: 5.857rem;
}

.owl-dot {
  display: inline-block;
