  margin: 0 0.6rem;
}

.owl-dot span {
  display: block;
  width: 0.6rem;
  height: 0.6rem;
  background-color: #cccccc;
}

.owl-dot.active span {
  background-color: #00adee;
}

.item-shadow {
  position: relative;
}

.item-shadow:before {
  content: '';
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 46%, rgba(0, 0, 0, 0.8) 100%);
}

.project-item .link-arrow {
  position: absolute;
  z-index: 2;
